.mobileNav{
    display:none;
}

@media screen and (max-width:570px){
    .mobileNav{
        display: block;
        background:#000;
        color:#fff;
        position:fixed;
        height:80px;
        width:100%;
        left:0%;
        bottom:-2%;
        padding:10px;
        z-index:99999999;
    }

    .navIcon{
        content:e487;
    }

   .mobileNav .home,.mobileNav .categories,.mobileNav .account,.mobileNav .rewards{
    text-align:center;
   }
   .mobileNav .home svg{
    width:20px;
    height:20px;
   }
   .mobileNav .account svg{
    width:20px;
    height:20px;
   }
   .mobileNavLink{
    text-decoration: none;
    color:#fff;
   }

   .mobileNavLink:hover{
    color:#fff !important;
   }

   .home,.categories,.rewards,.account{
    font-size:14px;
   }
}