.uspImage{
    height:50px;
    width:50px;
}

.uspBanner{
background:#e0e0e0;
}

.uspBanner.background{
    background-image: url("../../assets/images/4My-Story.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.uspTitle{
    font-family: 'FuturaCondensed' !important;
    color:#fff;
    /* padding-right:50px; */
}

.uspTitle.homeUSP{
    font-family: 'TabarroPro' !important;
    color:#000;
}

@media screen and (min-width:920px){
    .uspImage{
        height: 115px;
        width:160px;
    }
    .uspBanner .row > [class^="col-"] {
        width: 9.666667%;
    }
}

@media screen and (max-width:910px){
    .uspTitle{
        padding-right:0px;
    }
    .uspImage{
        height:85px;
        width:120px;
        margin-left: 10px;
    }
}