.footer{
    background:#000;
    color:#fff;
    margin-bottom:0px;
}

.footer .details{
    text-decoration: none;
    color:#fff;
    margin-right:30px;
}

.footer .details:hover{
    color:#fff !important;
}

.footer .details{
    text-decoration: none;
    color:#fff;
    margin-right:30px;
}

.footer .links{
    border-top:1px solid #fff;
    border-bottom:1px solid #fff;
}

.footer .social_links{
    color:#fff;
    margin-right:10px;
}

.footerImage{
    width:200px;
    filter: grayscale(1) invert(1);
}

.footer_links{
    height:24px;
    width:24px;
}

.copyright{
    margin-bottom:0px;
}

.facebook_footer:hover{
color:#1877f2 !important;
}

.instagram_footer:hover{
    color:#FFC0CB !important;
    }

.twitter_footer:hover{
    color:#00acee !important;
    }

.youtube_footer:hover{
    color:#FF0000 !important;
    }



@media screen and (max-width:570px){
    .footer .details {
        margin-right:10px;
    }
    .footer .copyright{
        font-size:14px;
    }
    .footer .links{
        font-size:12px;
        white-space:nowrap;
    }
    .footerImage{
        width:140px;
    }
    .footer_links{
        height:20px;
        width:20px;
    }
}
