.listIcon {
height:30px;
width:30px;
}

.sidebarList li {
    font-weight: 600;
    list-style-type: none;
    padding:10px 0px;
}

.offcanvas-header .btn-close{
    color:#fff;
}

.offcanvas-header{
    justify-content: end;
}

.invert-color{
    filter: invert(1);
}

.sidebarList li{
    display:flex;
    align-items: center;
    justify-content: start;
}

.sidebarList li img{
    margin-right:10px;
}

.sidebarList li{
    cursor: pointer;
}

.offcanvas-body::-webkit-scrollbar {
    display: none;
  }